<template>
  <div class="header pr">
<!--    <slot></slot>-->
    <div class="right">
      <div class="link">
        <i class="en-icon"></i>
<!--        <i class="file-icon"></i>-->
        <router-link to='/alarm'><i class="warn-icon" ></i></router-link>
      </div>
      <div class="user">
        <el-dropdown>
                <span class="el-dropdown-link">
                    <i class="head-icon">
                        <img src="../assets/images/portrait.png" alt="">
                    </i>
                    {{userID}}
                    <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
          <el-dropdown-menu slot="dropdown">
            <!--<el-dropdown-item>-->
              <!--<i class="el-icon-edit-outline"></i>-->
              <!--Change password-->
            <!--</el-dropdown-item>-->
            <el-dropdown-item><p @click="logOut()">Sign out</p></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'mainHeader',
  data () {
    return {
      userID: null
    }
  },
  computed: {
    ...mapGetters([
      'loginUserName'
    ])
  },
  methods: {
    logOut () {
      sessionStorage.clear()
      sessionStorage.setItem('returnUrl', window.location.href)
      this.$router.push({ path: '/login' })
    }
  },
  mounted () {
    this.userID = sessionStorage.getItem('access_userID')
    if (!this.userID) {
      this.$router.push({ path: '/login' })
    }
  }
}
</script>

<style scoped>
</style>



